import axios from '../utils/axios';

export const endpoints = {
  key: 'device',
  restart: '/restart'
};

export async function actionRestart() {
  try {
    const url = endpoints.key + endpoints.restart;
    const response = await axios.get(url);
    return response.data;
  } catch (error) {
    console.log(error);
    return {
      status: false,
      msg: error.message
    };
  }
}
